body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 }
 
 code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
 }
 
 .App {
  text-align: center;
 }
 
 .App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
 }
 
 .App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
 }
 
 .App-link {
  color: #61dafb;
 }
 
 @keyframes App-logo-spin {
  from {
   transform: rotate(0deg);
  }
  to {
   transform: rotate(360deg);
  }
 }
 
 #content {
  margin-top: 0px;
  text-align: center;
 }
 
 section.timeline-outer {
  width: 100%;
  margin: 0px;
 }
 
 h1.header {
  font-size: 50px;
  line-height: 70px;
 }
 
 .timeline {
  border-left: 8px solid #42a5f5;
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  color: #333;
  margin: 10px auto;
  letter-spacing: 0.5px;
  position: relative;
  line-height: 1.4em;
  padding: 20px;
  list-style: none;
  text-align: left;
 }
 
 .timeline .event {
  border-bottom: 1px solid rgba(160, 160, 160, 0.2);
  padding-bottom: 15px;
  margin-bottom: 20px;
  position: relative;
 }
 
 .timeline .event:before,
 .timeline .event:after {
  position: absolute;
  display: block;
  top: 0;
 }
 
 .timeline .event:before {
  left: -177.5px;
  color: #212121;
  content: attr(data-date);
  text-align: right;
  font-size: 16px;
  min-width: 120px;
 }
 
 .timeline .event:after {
  box-shadow: 0 0 0 8px #42a5f5;
  left: -30px;
  background: #212121;
  border-radius: 50%;
  height: 11px;
  width: 11px;
  content: "";
  top: 5px;
 }
 
 @media (max-width: 945px) {
  .timeline .event::before {
   left: 0.5px;
   top: 20px;
   min-width: 0;
   font-size: 13px;
  }
  .timeline h3 {
   font-size: 16px;
  }
  .timeline p {
   padding-top: 20px;
  }
  section.lab h3.card-title {
   padding: 5px;
   font-size: 16px;
  }
 }
 
 @media (max-width: 768px) {
  .timeline .event::before {
   left: 0.5px;
   top: 20px;
   min-width: 0;
   font-size: 13px;
  }
 
  .timeline h3 {
   font-size: 16px;
  }
  .timeline p {
   padding-top: 10px;
   font-size: 14px;
  }
 }
 
 a.portfolio-link {
  margin: 0 auto;
  display: block;
  text-align: center;
 }
 .fontTinySpecial {
  font-size: 10px;
  margin-top: 0px;
 }
 .avatar {
  font-weight: 500;
  background-color: gray;
 }